import axios from '@/utils/uniAxios.js';
import {
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			img: '/static/missing-face.png',
			phone:'',
			id:'',
			user:{
				name:'',
				nickName:''
			}
		};
	},
	onLoad(option) {
		this.id = option.id;
		this.getData()
	},
	methods: {
		...mapMutations(['logout']),
		// 跳转路由
		navTo(url) {
			uni.navigateTo({
				url: `../set/${url}`+'?id='+this.id
			});
		},
		// 初始化
		getData(){
			// 如果是手机登录
			if(uni.getStorageSync('setPhone')){
				axios.post('/sso/user/id',{id:this.id}).then(res=>{
					this.phone = res.data.data.phone;
					this.user.name = this.phone;
				})
			}
			
		}
	}
}
